.container {
  background-color: #ffffff17;
  display: flex;
  height: 5vw;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.menuBox {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 2vw;
  font-family: "LilitaOne";
  font-size: 2vw;
}

.menuLink {
  text-decoration: none;
  color: white;
}
